import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { SolicitacaoContatosService } from './../../../../../shared/services/adm-comunicacao/solicitacao-contatos/solicitacao-contatos.service';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../../constants/toast.constant';
import { ToastMessageService } from './../../../../services/toast/toast-message.service';

@Component({
  selector: 'app-contact-form',
  templateUrl: './contact-form.component.html',
  styleUrls: ['./contact-form.component.scss'],
})
export class ContactFormComponent implements OnInit, OnDestroy {

  @Output()
  sendEvent: EventEmitter<any> = new EventEmitter();

  form: FormGroup;
  maxCaracteresMotivo = 0;

  private subscriptions = new Subscription();

  constructor(
    private readonly formBuilder: FormBuilder,
    private readonly solicitacaoContatosService: SolicitacaoContatosService,
    private readonly toastService: ToastMessageService,
    private readonly translateService: TranslateService,
  ) { }

  ngOnInit() {
    this.form = this.formBuilder.group({
      nome: [, [Validators.required]],
      email: [, [Validators.required, Validators.email]],
      ddd: [, [Validators.required, Validators.minLength(2), Validators.maxLength(2), Validators.pattern('^[0-9]*$')]],
      telefone: [, [Validators.required, Validators.minLength(8), Validators.pattern('^[0-9]*$')]],
      empresa: [, []],
      descricao: [, [Validators.required, Validators.minLength(6), Validators.maxLength(200)],
      ],
    });
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  send() {
    const nome = this.form.get('nome').value;
    const email = this.form.get('email').value;
    const ddd = this.form.get('ddd').value;
    const telefone = `${ddd}${this.form.get('telefone').value}`;
    const descricao = this.form.get('descricao').value;

    let empresa = this.form.get('empresa').value;
    empresa = empresa == null || empresa.trim() == '' ? undefined : empresa;

    const dto = {
      nome, email, telefone, empresa, descricao,
    }


    const sub = this.solicitacaoContatosService.create(dto)
      .subscribe({
        next:  async (response) => {
          const titulo = await this.translateService.get('toast.contact-form.title-success').toPromise();
          const detalhe = await this.translateService.get('toast.contact-form.detail-success').toPromise();
          
          //const titulo = 'Sucesso!';
          //const detalhe = 'Recebemos sua solicitação';
          this.toastService.presentToast({
            titulo,
            detalhe,
            duracao: ToastEnum.shortDuration,
            gravidade: ToastPrimeSeverityEnum.SUCESSO,
          });

          this.form.reset();
          this.maxCaracteresMotivo = 0;
          this.sendEvent.emit();
        }
      });

    this.subscriptions.add(sub);
  }

  handleMotivoInput(event?) {
    const descricao = this.form.get('descricao').value as string;
    let length = descricao ? descricao.length : 0;

    if (length >= 200) {
      const newDescricao = descricao.substring(0, 200);
      this.form.get('descricao').setValue(newDescricao);
      this.form.get('descricao').updateValueAndValidity();
      length = 200;
    }

    this.maxCaracteresMotivo = Number(length);
  }

  disableBtn(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

}
