import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from './../../../../../config/api.config';
import { CreateSolicitacaoContatoDto, SolicitacaoContatoDto } from './../../../interfaces/adm-comunicacao/solicitacao-contato/solicitacao-contato.dto';

@Injectable({
  providedIn: 'root'
})
export class SolicitacaoContatosService {

  constructor(
    private readonly http: HttpClient
  ) { }

  create(dto: CreateSolicitacaoContatoDto): Observable<SolicitacaoContatoDto> {
    return this.http.post<SolicitacaoContatoDto>(`${API_CONFIG.baseURL}/solicitacao-contatos`, dto, {
      responseType: 'json',
    });
  }
}
